import uniRate from "../../components/uni-rate/uni-rate.vue";
import {  
    mapState,  
    mapMutations  
} from 'vuex'; 

export default {
		data() {
			return {
				imageList: [],
				postImageList:[],
				ratenum:5,
				countIndex: 8,
				count: [1, 2, 3, 4, 5, 6],
				input_content:'',
				anony:0,
				logining: false,
				order_id:0,
				store_name:''
			}
		},	
		components: {uniRate},
		computed: mapState(['hasLogin','userInfo']),  
		onLoad(e) {
			this.order_id=e.id;
			if (!this.hasLogin) {  
				uni.navigateTo({
					url:'login',
				});
				return false;
			}
			this.loadData();
		},
		methods: {
			async loadData() {
				var _this = this;
				_this.$Request.post('member_evaluate/pinlun',{order_id:_this.order_id}).then(res => {
					if (res.code == 200) {
						console.log(res);
						_this.store_name=res.datas.store_info.store_name;
					}else{
						uni.showLoading({title:res.datas.error+''});
						setTimeout(function () {
							uni.navigateBack();
						}, 1500);
					}
				});
				
			},
			onChange(e) {
				console.log('rate发生改变:' + JSON.stringify(e));
				this.ratenum = e.value;
			},
			chooseImage: async function() {
				let _this = this;
				if (this.imageList.length === 6) {
					return false;
				}
				uni.chooseImage({
						sourceType: ['camera', 'album'],

						sizeType: ['original'],

					count: 1,//_this.imageList.length + _this.count[_this.countIndex] > 6 ? 6 - _this.imageList.length : _this.count[_this.countIndex],
					success: (result) => {		
						uni.showLoading({title:'图片处理中...'});
						console.log(JSON.stringify(result.tempFilePaths));
						_this.$Request.upLoadFile('eval_album/file_upload',result.tempFilePaths[0]).then(res => {
							console.log(res);
							console.log('===upload======');
							if (res.code == 200) {
								console.log('===upload=2=====');
								uni.hideLoading();
								console.log(res);
								_this.imageList = _this.imageList.concat(result.tempFilePaths)//非APP平台不支持自定义压缩,暂时没有处理,可通过uni-app上传组件的sizeType属性压缩
								
								_this.postImageList = _this.postImageList.concat(res.datas.file_name);
								
							}else{
								uni.showLoading({title:res.datas.error+''});
								setTimeout(function () {
									uni.hideLoading();
								}, 2000);
							}
							//uni.hideLoading();
						});
					}
				})
			},
			previewImage: function(e) {
				var current = e.target.dataset.src;
				uni.previewImage({
					current: current,
					urls: this.imageList
				})
			},
			close(e){
				this.imageList.splice(e,1);
				this.postImageList.splice(e,1);
			},
			publish() {  
				let _this = this;
				if (!this.input_content) {
					uni.showModal({ content: '内容不能为空', showCancel: false, });
					return;
				}
				
				uni.showLoading({title:'发布中'});
				_this.logining = true;
			
				let postData={
					order_id:this.order_id,score:this.ratenum,comment:this.input_content,evaluate_image:this.postImageList,
					anony:this.anony
				}				   
				this.$Request.post('member_evaluate/save',postData).then(res => {
					if (res.code == 200) {
						console.log(res);
						uni.showLoading({title:'发布成功！！'});
						setTimeout(function () {
							uni.navigateTo({
								url:'evallist',
							})
						}, 1500);
						
					}
					else{
						uni.showToast({
							icon: 'none',
							title:res.datas.error,
							duration:2000,
							success: function () {
								 setTimeout(function () {
									uni.navigateTo({
										url:'txway',
									});
								 }, 2000);
							}
						});
						uni.hideLoading();
						_this.logining = false;
					}
				}); 
				
				
			},
		checkboxChange(e){
			console.log(e.detail.value[0]);
			if(e.detail.value.length>0)
			{
				this.anony = 1;
			}else{
				this.anony = 0;
			}
		}
		}
	}
